



header {
  padding-top: 5px !;
  display: table;
  width: 100%;
}

.sr-only {
  display: none;
}

.right {
  float: left;
  display: table-cell;
  width: 85%;
}
.image {
  width: 72px;
  padding-top:17px;
  display: table-cell;
  overflow: hidden;
  padding-left: 20px;
  float: left;
  transform: translateY(-4px);
}

#menu1 #menu2 {
  display: none;
}
.pcp{
  float: left;
  padding-left: 10px;
  color: #f9a404;
  padding-left: 20px;
  font-size: 30px;
  font-weight: 200;
  line-height: 15px;
  font-family: 'Montserrat', sans-serif;
}
.pcp strong{
  color: #3a54a5;
  font-weight: 400;
  
}

.text{
  line-height: 2px;
  overflow: hidden;
  position: relative;
  display: block;
  width: 100%;
  
}
.text h1 {
  width: 100%;
  text-align: left;
}

.navbar {
    clear: both;
    width: 100%;
    overflow: hidden;
    text-align: center;
    table-layout: fixed;
    padding-bottom: 5px;
    padding-left: 30px
  }


/* The dropdown container */
.dropdown {
  float: left;
  overflow: hidden;
  padding-top: 0px;
  padding-bottom: 11px;
  width: 16.6%;

}

.line {
  
border-top: 1px solid;
border-color: grey;
clear: both;
display: block;
width:100%;


}

/* Dropdown button */
  .dropbtn {
  font-size: 14px; 
  border: none;
  outline: none;
  padding: 0;
  color:#1d1d21;
  display: block;
  padding-bottom: 0px; 
  background-color: inherit;
  text-align: center;
  border-bottom: 2px solid;
  border-color: white;
  font-family: inherit; /* Important for vertical align on mobile phones */
  margin: 0; /* Important for vertical align on mobile phones */
  
}

/* Add a red background color to navbar links on hover */
.navbar  .dropbtn:hover {
  border-bottom: 2px solid;
  border-color: #f9a404;
}

#activeButton {
  background-color: #ddd;
}

/* Dropdown content (hidden by default) */

.dropdown-content {
  display: block ;
  position: absolute;
  background-color: white;
  width: 12%;
  border-left: grey solid;
  border-right: grey solid;
  border-bottom: grey solid;
  border-width: 1px;
  z-index: 1;

  -webkit-animation: scale-up-ver-top 0.2s linear both;
          animation: scale-up-ver-top 0.2s linear both;
          
  transform: translateY(15.95px) translateX(-4px);
}


/* Links inside the dropdown */
.dropdown-content a {
  float: none;
  color: black;
  padding: 10px;
  text-decoration: none;
  display: block;
  text-align: left;
  font-size: 14px;
  font-weight: 400;
}

/* Add a grey background color to dropdown links on hover */
.dropdown-content a:hover {
  background-color: #ddd;
}



.underlined {
  border-bottom: 2px solid;
  border-color: #f9a404;
}
#menu1, #menu2 {
  display: none;
}

.mtext {
  display: none;
}

@media only screen and (max-width: 700px) {
  .Map {
    width: 100% !important;
  }
  .image {
    padding: 5px;
    display: table-cell !important;
    height: 50px;
    width: 10vw;
  }
  .image img {
    width: 10vw;
    max-width: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  .pcp strong {
    display: block;
  }
  .pcp {
    margin-top: 5px;
    padding-left: 4px;
    line-height: 24px;
    font-size: 19px;
    margin-bottom: 5px;
  }
  .mtext {
    padding: none;
    float: left;
    display: block;
  }
  .text {
    display: none;
  }
  #menu1 {
    display: block;
  }
  #menu1, #menu2 {
    width: 15px;
  }
  .Menu {
    float:right;
    display: block;
    padding-right: 3vw;
    padding-top: 20px;
  }
  .right{
    width: 100%;
  }
  .navbar {
    display: none;
    padding-left: 0px;
    width: 100%;
    border-top: 1px solid grey;
    -webkit-animation: scale-up-ver-topp 0.2s linear both;
          animation: scale-up-ver-topp 0.2s linear both;
  }
  .dropdown{
    width:100%
  }
  .dropbtn {
    font-size: 14px;
    display: block;
    float: left;
    width: 100%;
    padding-top: 2vw;
    padding-bottom: 2vw;
  }
  .dropdown-content {
    float: left;
    width: 100%;
    position: static;
    border: none;
    display: block;
    -webkit-animation: scale-up-ver-topp 0.2s linear both;
          animation: scale-up-ver-topp 0.2s linear both;
  }
  .dropdown-content a {
    font-size: 14px;
    text-align: center;
    border: 1px solid white;
    background-color: rgb(240, 240, 240);
  }
  .underlined {
    border-bottom: 0px solid;
    border-color: #f9a404;
    color: #f9a404;
  }
  .show {
    display: block !important;
  }
  .hide {
    display: none !important;
  }
}
@-webkit-keyframes scale-up-ver-top {
  0% {
    -webkit-transform: scaleY(0.4) translateY(15.95px) translateX(-4px);;
            transform: scaleY(0.4) translateY(15.95px) translateX(-4px);;
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%;
  }
  100% {
    -webkit-transform: scaleY(1) translateY(15.95px) translateX(-4px);;
            transform: scaleY(1) translateY(15.95px) translateX(-4px);;
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%;
  }
}
@keyframes scale-up-ver-top {
  0% {
    -webkit-transform: scaleY(0.4) translateY(15.95px) translateX(-4px);
            transform: scaleY(0.4) translateX(-4px);
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%;
  }
  100% {
    -webkit-transform: scaleY(1) translateY(15.95px) translateX(-4px);
            transform: scaleY(1) translateY(15.95px) translateX(-4px);
            
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%;
  }
}


@-webkit-keyframes scale-up-ver-topp {
  0% {
    -webkit-transform: scaleY(0.4) ;
            transform: scaleY(0.4) ;
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%;
  }
  100% {
    -webkit-transform: scaleY(1) ;
            transform: scaleY(1) ;
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%;
  }
}
@keyframes scale-up-ver-topp {
  0% {
    -webkit-transform: scaleY(0.4) ;
            transform: scaleY(0.4) ;
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%;
  }
  100% {
    -webkit-transform: scaleY(1) ;
            transform: scaleY(1) ;
            
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%;
  }
}

